@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: black;
}

.App {
  color: whitesmoke;
  text-align: center;
}


.Portfolio {
    margin-bottom: 50px;
}
.PortfolioThing {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    margin: 25px auto;
    width: 50%;
    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    border: 1px solid rgb(88, 84, 84);
    border-radius: 15px;
}

#myeats {
    background-image: url('https://i.imgur.com/wsnJZMk.png');
}

#memeworld {
    background-image: url('https://i.imgur.com/0RtDXSd.png');
}

#fit {
    background-image: url('https://i.imgur.com/xHRx4r6.png');
}

#bonez {
    background-image: url('https://i.imgur.com/ZD4AYr9.png');
}

.info {
    font-weight: bold;
    padding: 5px 0;
    width: 100%;
    border-radius: 0 0 10px 10px;
    background-color: rgba(63, 63, 63, 0.932);
}

.PortfolioThing:hover p {
    display: block;
}

.info h2 {
    font-family: 'Staatliches';
    font-size: 25px;
    margin: 0;
}

.info h3 {
    margin: 1px;
    color: orchid;
}

.info p {
    display: none;
    font-size: 14px;
    margin: 0 auto;
    width: 90%;
    word-wrap: break-word;
    color: moccasin;
}

.info a {
    color: rgb(84, 148, 204);
    text-decoration: none;
    border-radius: 5px;
}

.info span {
    color: rgb(223, 96, 96);
    margin: 0;
}

.info a:hover {
    color: salmon;
}


@media only screen and (max-width: 700px) {
    .PortfolioThing {
        width: 90%;
    }
}

@media only screen and (min-width: 1500px) {
    .PortfolioThing {
        width: 35%;
    }
}
.Header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 400px;
    width: 100%;
    padding: 25px 0;
}

.Header h1 {
    margin-bottom: 0;
}

.Header p {
    margin: 10px;
}

.Header a {
    color: rgb(84, 148, 204);
    text-decoration: none;
    padding: 5px;
    border-radius: 5px;
}

.Header a:hover {
    background-color: cyan;
    color: black;
}

.profile-pic {
    background-image: url('https://scontent.fapa1-2.fna.fbcdn.net/v/t1.0-9/118156087_10157567923392285_8250194324875300449_n.jpg?_nc_cat=104&ccb=2&_nc_sid=09cbfe&_nc_ohc=DnGXrVM2tP0AX9hT-q6&_nc_ht=scontent.fapa1-2.fna&oh=201c1deff5cf39202ae2b9fdd44ce5b2&oe=5FB69DB9');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border: 2px solid whitesmoke;
    width: 250px;
    height:  275px;
    border-radius: 25px;
}
